==========================**/
.f-light {
  color: var(--body-light-font-color);
}

svg.f-light {
  fill: var(--body-light-font-color);
  opacity: 0.8;
}

.light-card {
  background-color: var(--light-background);
}

.light-background {
  background-color: var(--light-bg);
}

.link-with-icon {
  color: var(--body-light-font-color);
  padding: 9px 10px;
  background-color: var(--light-bg);
  border-radius: 5px;
}
.link-with-icon svg {
  width: 13px;
  height: 13px;
  stroke: var(--body-light-font-color);
  vertical-align: -2px;
}
.link-with-icon:hover {
  color: var(--body-light-font-color);
}

.icon-arrow-down.icon-rotate,
.icon-arrow-up.icon-rotate {
  transform: rotate(-45deg);
  display: inline-block;
}

